@import url(http://fonts.font.im/css?family=Pacifico);
@import url(https://fonts.googleapis.com/css2?family=Ma+Shan+Zheng&display=swap);
.App {
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App .Header {
  position: sticky;
  top: 0;
  height: 100px;
  border-bottom: 1px solid #000;
  display: flex;
  align-items: center;
  background-color: #eee;
  box-shadow: 10px 10px 10px #111;
  z-index: 4;
  font-family: 'Ma Shan Zheng', cursive;
  min-width: 1100px;
}

.App .Header .welcome {
  flex: 1 0 auto;
  text-align: center;
  font-family: 'Ma Shan Zheng', cursive;
}

.logo {
  margin: 0;
  padding: 0;
  position: relative;
  left: 1px;
  transform: scale(0.5);
  transition: transform 1s;
  animation: animation 2s linear infinite;
}



.logo:hover {
  transform: scale(0.7);
}

@keyframes animation {
  0% {
    transform: scale(0.5) rotate(0deg);
  }

  100% {
    transform: scale(0.5) rotate(360deg);
  }
}

.nav:hover {
  color: #5a5ae2;
  cursor: pointer;
  text-decoration: underline;
  text-underline-offset: 20%;
}

.img {
  margin: 10px;
  position: relative;
  height: 216px;
  width: auto; 
  box-shadow: 10px 10px 10px #5a5ae2;
  transition: transform 1s ;
}

.img:hover {
  transform: scale(1.1);
}

.photoPage {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  list-style:  none;
}

.logIn {
  position: relative;
  right: 50px;
}

.container {
  margin: 0;
  padding: 0;
}

.container section h1 {
  margin: 20px 0 0;
  padding: 0;
  text-align: center;
  font-size: 5em;
  animation: colorAnimate 10s linear infinite;
  font-family: 'Ma Shan Zheng', cursive;
}
.photoPage {
  color:#eee;
}

@keyframes colorAnimate {
  0% {
    color: #c30000;
  }
  25% {
    color: #1c547d;
  }
  50% {
    color: #00a504;
  }
  75% {
    color: #e7b900;
  }
  100% {
    color: #c30000;
  }
}
.box {
  position: relative;
  width: 100%;
  height: 600px;
  background: #1c547d;
  overflow: hidden;
  animation: bgAnimate 10s linear infinite;
}

@keyframes bgAnimate {
  0% {
    background-color: #c30000;
  }
  25% {
    background-color: #1c547d;
  }
  50% {
    background-color: #00a504;
  }
  75% {
    background-color: #e7b900;
  }
  100% {
    background-color: #c30000;
  }
}


.box .tree {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  bottom: -231px;
  opacity: 0.5;
}

.box .tree.tree1 {
  background: url(./assets/img/tree1.png);
  background-repeat: repeat-x;
  animation: animationTree 15s linear infinite;
  opacity: 1;
  z-index: 2;
}

.box .tree.tree2 {
  background: url(./assets/img/tree2.png);
  background-repeat: repeat-x;
  animation: animationTree 25s linear infinite;
}

.box .tree.tree3 {
  background: url(./assets/img/tree3.png);
  background-repeat: repeat-x;
  animation: animationTree 45s linear infinite;
}

@keyframes animationTree {
    0% {
      background-position: 0 0;
    }

    100% {
      background-position: 1406px 0;
    }
}

.box .snow {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(./assets/img/snow.png);
  z-index: 3;
  animation: snowAnimation 10s linear infinite;
}

@keyframes snowAnimation {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 0 769px;
  }
}

.content {
  margin-top: 50px;
  height: auto;
  width: 100%;
}

.footer::after {
  content: '@By Wql & Zqq';
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 100%;
  background-color: #3f4552;
}

.swiperslider {
  width: auto;
  text-align: center;
}

.imgslider {
  height: 10%;
  width: 20%;
  box-shadow: 5px 5px 5px #5a5ae2;
  margin-bottom: 10px;
}

.content-img {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: #fff;
  padding-bottom: 100px;
}

.content-img .contentH2 {
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  box-shadow: 5px 5px 5px #000;
  text-align: center;
}
